.feedback_title {
    font-weight: 600;
    font-size: 24px;
    padding-left: 200px;
}

.feedback_notification {
  padding-left: 100px;
  font-size: 24px;
}



.feedback_ratio_list {
  display: block;
  padding-left: 100px;
}

feedback_ratio_item {
  display: inline-block;
}




.custom_btn {
    width: 130px;
    height: 40px;
    color: #fff;
    border-radius: 5px;
    padding: 10px 25px;
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
      7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
    outline: none;
  }

.feedback_button_item {
    background: rgb(0, 172, 238);
    background: linear-gradient(
      0deg,
      rgba(0, 172, 238, 1) 0%,
      rgba(2, 126, 251, 1) 100%
    );
    width: 130px;
    height: 40px;
    line-height: 42px;
    padding: 0;
    border: none;
  }
  .feedback_button_item span {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
  }
  .feedback_button_item:before,
  .feedback_button_item:after {
    position: absolute;
    content: '';
    right: 0;
    top: 0;
    background: rgba(2, 126, 251, 1);
    transition: all 0.3s ease;
  }
  .feedback_button_item:before {
    height: 0%;
    width: 2px;
  }
  .feedback_button_item:after {
    width: 0%;
    height: 2px;
  }
  .feedback_button_item:hover {
    background: transparent;
    box-shadow: none;
  }
  .feedback_button_item:hover:before {
    height: 100%;
  }
  .feedback_button_item:hover:after {
    width: 100%;
  }
  .feedback_button_item span:hover {
    color: rgba(2, 126, 251, 1);
  }
  .feedback_button_item span:before,
  .feedback_button_item span:after {
    position: absolute;
    content: '';
    left: 0;
    bottom: 0;
    background: rgba(2, 126, 251, 1);
    transition: all 0.3s ease;
  }
  .feedback_button_item span:before {
    width: 2px;
    height: 0%;
  }
  .feedback_button_item span:after {
    width: 0%;
    height: 2px;
  }
  .feedback_button_item span:hover:before {
    height: 100%;
  }
  .feedback_button_item span:hover:after {
    width: 100%;
  }